<!-- Reserve.vue -->
<template>
  <div class="property-register">
<!--    <van-nav-bar left-arrow @click-left="goBack" title="访客预约"></van-nav-bar>-->
    <navigation-bar :pageName="pageName" @onLeftClick="goBack">
    </navigation-bar>
    <div class="form-box">
      <van-field
        v-model="formObj.nameValue"
        label="访客姓名"
        placeholder="请输入访客姓名"
        input-align="right"
        :error="error_name"
        @blur="blurName"
      />
      <div class="role">
        <van-field
          v-model="formObj.reasonValue"
          :value="formObj.reasonValue"
          readonly
          clickable
          is-link
          label="访客角色"
          placeholder="请选择访客角色"
          input-align="right"
          @click="selectRole"
          :error="error_reason"
        />
        <span v-show="formObj.reasonValue">最长可预约{{durationStr}}</span>
      </div>
      <van-popup v-model="reasonPicker" position="bottom">
        <van-picker
          show-toolbar
          :columns="reasonList"
          value-key="name"
          @cancel="reasonPicker = false"
          @confirm="onConfirmReason"
        />
      </van-popup>
      <van-field
        label="访客手机号"
        v-model="formObj.phoneValue"
        type="tel"
        placeholder="请输入访客手机号"
        input-align="right"
        :maxlength="11"
        :error="error_phone"
        @blur="blurPhone"
      />
      <van-field
        label="访问地址"
        placeholder="请选择访问地址"
        input-align="right"
        :value="formObj.addressValue"
        v-model="formObj.addressValue"
        readonly
        clickable
        is-link
        @click="selectAddr"
        :error="error_address"
      />
      <van-popup v-model="addressPicker" position="bottom">
        <van-picker
          ref="addrPicker"
          show-toolbar
          value-key="name"
          :columns="addressList"
          @cancel="addressPicker = false"
          @confirm="onConfirmAddress"
        />
      </van-popup>
      <van-field
        label="访问时间"
        placeholder="请选择访问时间"
        input-align="right"
        v-model="startTimeValue"
        readonly
        clickable
        is-link
        @click="startTimeFun()"
        :error="error_startTime"
      />
      <van-popup v-model="startTimePicker" position="bottom">
        <van-datetime-picker
          type="datetime"
          :min-date="startminDate"
          :max-date="startmaxDate"
          :formatter="formatter"
          @cancel="startTimePicker = false"
          @confirm="onConfirmStartTime"
        />
      </van-popup>
      <van-field
        label="失效时间"
        placeholder="请选择失效时间"
        input-align="right"
        v-model="endTimeValue"
        readonly
        clickable
        is-link
        @click="endTimeFun()"
        :error="error_endTime"
      />
      <van-popup v-model="endTimePicker" position="bottom">
        <van-datetime-picker
          type="datetime"
          :min-date="endminDate"
          :max-date="endmaxDate"
          :formatter="formatter"
          @cancel="endTimePicker = false"
          @confirm="onConfirmEndTime"
        />
      </van-popup>
      <p class="info">以下信息选填</p>
      <div class="car-calue">
        <van-field
          v-model="formObj.carNumValue"
          label="车牌号码"
          placeholder="请输入车牌号码"
          :disabled="noParkInput"
          input-align="right"
          @blur="blurPlate"
        />
        <span class="left-num">剩余车位：{{leftNum}}</span>
      </div>

      <div class="green-channel">
        <div>
          <div class="title">预约绿色通道</div>
          <div class="tip">提示:绿色通道最长停车时间{{greenTimeIntervalStr}}</div>
        </div>
        <van-radio-group
          v-model="formObj.greenChannel"
          :disabled="noParkRadio"
          @change="changeRadio"
        >
          <van-radio name="2" checked-color="#00cccc">是</van-radio>
          <van-radio name="0" checked-color="#00cccc">否</van-radio>
        </van-radio-group>
      </div>
      <div class="upload-pic">
        <span>上传人脸照片</span>
        <!-- <span v-show="picUpload" class="upload-true">上传成功</span> -->
        <!-- <img :src="(picUrl!='')?picUrl:'@/assets/imgs/visitorRegister/camer.png'" class="thumb"> -->
        <div class="pic-box">
          <img v-if="!picUrl" :src="cameraIcon" @click="picDialog=true" class="camera" />
          <img v-else :src="picUrl" @click="picDialog=true" class="facetub" />
        </div>
      </div>
    </div>
    <div>
      <button class="submit-btn" @click="submitForm">提交预约</button>
      <!-- <button class="submit-btn" @click="getPic">下载图片</button> -->
    </div>
    <!-- <img v-if="imageUrl" :src="imageUrl" alt="人脸测试"> -->
    <van-dialog
      v-model="leftNumDialog"
      class-name="cancel-dialog"
      confirm-button-text="知道了"
      confirm-button-color="#fff"
    >
      <img src="../../assets/imgs/visitorRegister/car.png" />
      <div>当前车位不足</div>
      <div>仅可预约访客权限</div>
    </van-dialog>
    <van-dialog
      v-model="picDialog"
      class-name="pic-dialog"
      confirm-button-text="知道了"
      confirm-button-color="#fff"
      @confirm="showPhotoSelectSheet=true"
    >
      <!-- <div class="text">请上传不小于10kb，</div>
      <div class="text">不超过8M的人脸照片。</div>
      <div class="text">建议人脸正对屏幕中间拍照哦</div>-->
      <div class="text">请上传背景干净的人脸照片</div>
      <div class="text">或正对屏幕中间拍照</div>
    </van-dialog>
    <!-- 相册、拍照 -->
    <van-action-sheet
      v-model="showPhotoSelectSheet"
      :actions="actions"
      cancel-text="取消"
      round
      close-on-click-action
      @select="onClickedGetPhoto"
    />
  </div>
</template>

<script>
import dayjs from "dayjs";
import { ActionSheet, RadioGroup, Radio } from "vant";
import {
  getFaceRole,
  getParkingInfo,
  getGreenTime,
  getDoorNumInfo,
  addFaceInfo,
  face2Device,
  bookingInfo
} from "xt/services/register";
import { checkName, checkPlateNo, checkPhoneNo } from "xt/utils/vertification";
import { store, storage } from "@/utils/store";
import NavigationBar from "../../../title/NavigationBar";

export default {
  name: "register",
  components: {
    NavigationBar,
    [ActionSheet.name]: ActionSheet,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio
  },
  data() {
    return {
      pageName: "访客预约",
      keepalive: false,
      custNews: null, // 一些登录的信息
      formObj: {
        nameValue: "", //访问姓名
        phoneValue: "", //访客手机号
        carNumValue: "", //车牌号码
        addressValue: "", //访问地址
        startTimeValue: "", //来访时间
        endTimeValue: "", //失效时间
        reasonValue: "", //来访事由
        reasonId: "", //来访事由
        reasonTime: "",
        greenChannel: 0
      },

      addressPicker: false,
      addressList: [],
      roomCode: "",
      roomName: "",

      startTimePicker: false,
      startTimeValue: "",
      startminDate: dayjs().toDate(),
      startmaxDate: dayjs()
        .add(1, "year")
        .toDate(),

      endTimePicker: false,
      endTimeValue: "",
      endminDate: "",
      endmaxDate: "",

      reasonPicker: false,
      reasonList: [],
      duration: 0,
      durationStr: "",

      error_name: false,
      error_reason: false,
      error_phone: false,
      error_address: false,
      error_startTime: false,
      error_endTime: false,

      leftNum: 0,
      leftNumDialog: false,

      noParkInput: false, // 车牌号input
      noParkRadio: true, // 绿色车位radio
      greenTimeInterval: "",
      greenTimeIntervalStr: "0分钟",

      cameraIcon: require("xt/assets/imgs/visitorRegister/camer.png"),
      picUrl: "", // imgData
      picUpload: false,
      facePic: false, // img上传成功了么
      faceId: "", // 人脸id
      fileupload: null,
      imageUrl: "",
      picDialog: false, // 上传照片提示弹窗
      showPhotoSelectSheet: false, //相册、拍照上传
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }]
    };
  },
  created() {
    console.log("------register.vue--created------");
    console.log("baseurl", storage.getStorage("xtBaseUrl"));
    this.custNews = localStorage.getItem("userInfo");
    this.getParkingLeftNum();
    this.getGreenTime();
    this.getFaceRole();
    this.getAddress();

    let queryObj = this.$route.query; // 再次预约带来的信息
    if (queryObj !== {}) {
      console.log(queryObj);
      this.formObj.nameValue = queryObj.guest;
      this.formObj.phoneValue = queryObj.guest_phone;
      this.formObj.carNumValue = queryObj.plate_num;
      this.formObj.greenChannel = "0";
      this.formObj.addressValue = queryObj.door_num;
      this.roomCode = queryObj.roomCode;
      this.roomName = queryObj.roomName;
      this.formObj.reasonValue = queryObj.guest_type_name;
      this.formObj.reasonId = queryObj.guest_type;
      this.formObj.reasonTime = queryObj.guest_type_time;
      let time = queryObj.guest_type_time; // 小时
      let day = parseInt(time / 24);
      let hour = parseInt(time % 24);
      this.duration = time;
      this.durationStr =
        day == 0
          ? `${hour}小时`
          : hour == 0
          ? `${day}天`
          : `${day}天${hour}小时`; // 字符串
    }
  },
  computed: {
    carNum() {
      return this.formObj.carNumValue;
    }
  },
  watch: {
    carNum(newVal, oldVal) {
      if (newVal) {
        this.noParkRadio = false;
      } else {
        this.noParkRadio = true; // 没填车牌号就不能选绿色车位
        this.formObj.greenChannel = "0";
      }
    }
  },
  methods: {
    goBack() {
      this.$router.push({ path: "/" });
    },
    selectRole() {
      this.reasonPicker = true;
      if (this.reasonList == []) {
        this.getFaceRole();
      }
    },
    // 获取访客角色
    getFaceRole() {
      // this.reasonPicker = true;
      this.reasonList = [];
      this.loading = true;

      let data = {
        type: 1
      };
      getFaceRole(data)
        .then(res => {
          this.loading = false;
          if (res.code == 200) {
            this.reasonList = res.data || [];
          } else {
            this.$toast(res.message);
          }
        })
        .catch(err => {
          this.loading = false;
          this.$toast(err);
        });
    },
    onConfirmReason(item, index) {
      console.log("访客角色--确定");
      if (!item) {
        this.reasonPicker = false;
        return;
      }
      console.log(item);
      this.formObj.reasonValue = item.name;
      this.formObj.reasonId = item.guestType;
      this.formObj.reasonTime = item.extend;
      let time = parseInt(item.extend); // 小时
      let day = parseInt(time / 24);
      let hour = parseInt(time % 24);
      this.duration = time;
      this.durationStr =
        day == 0
          ? `${hour}小时`
          : hour == 0
          ? `${day}天`
          : `${day}天${hour}小时`; // 字符串
      this.reasonPicker = false;
      this.error_reason = false;
      this.formObj.startTimeValue = "";
      this.startTimeValue = "";
      this.formObj.endTimeValue = "";
      this.endTimeValue = "";
    },
    selectAddr() {
      this.addressPicker = true;
      if (this.addressList == []) {
        this.getAddress();
      }
    },
    // 选择访问地址
    getAddress() {
      this.loading = true;
      // this.addressPicker = true;
      this.addressList = [];

      let data = {
        areaCode: this.custNews.AREA_CODE
      };
      getDoorNumInfo(data)
        .then(res => {
          this.loading = false;
          if (res.code == 200) {
            this.addressList = res.data || [];
          } else {
            this.$toast(res.message);
          }
        })
        .catch(err => {
          this.loading = false;
          this.addressPicker = false;
          this.$toast(err);
        });
    },
    onConfirmAddress(item, index) {
      console.log("访客地址--确定");
      if (!item) {
        this.addressPicker = false;
        return;
      }
      console.log(item, index);
      let i = index[0];
      let j = index[1];
      let k = index[2];
      let obj = this.addressList[i].children[j].children[k];
      this.formObj.addressValue = obj.value;
      this.roomCode = obj.roomCode;
      this.roomName = obj.name;
      this.addressPicker = false;
      this.error_address = false;
    },
    // 选项格式化函数
    formatter(type, value) {
      if (type === "year") {
        return `${value}年`;
      } else if (type === "month") {
        return `${value}月`;
      } else if (type === "day") {
        return `${value}日`;
      } else if (type === "hour") {
        return `${value}时`;
      } else if (type === "minute") {
        return `${value}分`;
      }
      return value;
    },
    //把时间转换为标准格式
    getStandardTime(time) {
      let year = time.getFullYear();
      let month =
        time.getMonth() + 1 > 10
          ? time.getMonth() + 1
          : "0" + (time.getMonth() + 1);
      let day = time.getDate() >= 10 ? time.getDate() : "0" + time.getDate();
      let hours =
        time.getHours() >= 10 ? time.getHours() : "0" + time.getHours();
      let minutes =
        time.getMinutes() >= 10 ? time.getMinutes() : "0" + time.getMinutes();
      return year + "-" + month + "-" + day + " " + hours + ":" + minutes;
    },
    startTimeFun() {
      if (!this.formObj.reasonValue) {
        this.$toast("请先选择访客角色");
      } else {
        this.startTimePicker = true;
      }
    },
    onConfirmStartTime(val) {
      // this.formObj.startTimeValue = startTime.getTime(); //毫秒
      // this.startTimeValue = this.getStandardTime(startTime); //转为标准格式显示
      // this.startTimePicker = false;
      // this.error_startTime = false;
      // this.endminDate = startTime; //获取最小失效时间
      // this.endmaxDate = new Date(this.formObj.startTimeValue + this.duration); //获取最大失效时间(开始时间, 间隔毫秒)
      // if (this.formObj.greenChannel == 2) {
      //   this.formObj.endTimeValue =
      //     this.formObj.startTimeValue + this.greenTimeInterval; //选中了绿色通道 失效时间自动+30min
      //   this.endTimeValue = this.getStandardTime(
      //     new Date(this.formObj.endTimeValue)
      //   ); //转为标准格式显示
      // } else {
      //   this.formObj.endTimeValue = "";
      //   this.endTimeValue = "";
      // }
      let startTime = dayjs(val);
      this.formObj.startTimeValue = startTime.valueOf(); //毫秒
      this.startTimeValue = startTime.format("YYYY-MM-DD HH:mm"); //转为标准格式显示
      this.startTimePicker = false;
      this.error_startTime = false;

      this.endminDate = val; //获取最小失效时间
      this.endmaxDate = startTime.add(this.duration, "hour").toDate(); //获取最大失效时间(开始时间, 间隔毫秒)
      if (this.formObj.greenChannel == 2) {
        let endTime = startTime.add(this.greenTimeInterval, "m"); //选中了绿色通道 失效时间自动+30min
        this.formObj.endTimeValue = endTime.valueOf();
        this.endTimeValue = startTime.format("YYYY-MM-DD HH:mm"); //转为标准格式显示
      } else {
        this.formObj.endTimeValue = "";
        this.endTimeValue = "";
      }
    },
    endTimeFun() {
      if (!this.formObj.startTimeValue) {
        this.$toast("请先选择访问时间");
      } else if (this.formObj.greenChannel == 2) {
        this.$toast(`绿色通道最长预约时间为${this.greenTimeIntervalStr}`);
      } else {
        this.endTimePicker = true;
      }
    },
    onConfirmEndTime(val) {
      let endTime = dayjs(val);
      this.formObj.endTimeValue = endTime.valueOf();
      this.endTimeValue = endTime.format("YYYY-MM-DD HH:mm");
      this.endTimePicker = false;
      this.error_endTime = false;
    },
    // 改变绿色通道 --是、否
    changeRadio(val) {
      this.formObj.greenChannel = val;
      if (this.formObj.startTimeValue && this.formObj.greenChannel == 2) {
        let endTime = dayjs(this.formObj.startTimeValue).add(
          this.greenTimeInterval,
          "m"
        );
        this.formObj.endTimeValue = endTime.valueOf();
        this.endTimeValue = endTime.format("YYYY-MM-DD HH:mm"); //转为标准格式显示
      } else {
        this.formObj.endTimeValue = "";
        this.endTimeValue = "";
      }
    },
    blurName() {
      this.error_name = false;
      if (this.formObj.nameValue && !checkName(this.formObj.nameValue)) {
        this.$toast("请填写正确格式的名字");
        return;
      }
    },
    blurPhone() {
      this.error_phone = false;
      if (this.formObj.phoneValue && !checkPhoneNo(this.formObj.phoneValue)) {
        this.$toast("请填写正确格式的电话号码");
        return;
      }
    },
    blurPlate() {
      console.log("车牌号码:", this.formObj.carNumValue);
      if (this.formObj.carNumValue && !checkPlateNo(this.formObj.carNumValue)) {
        this.$toast("请填写正确格式的车牌号");
        return;
      }
    },
    // 表单验证
    checkForm() {
      if (!this.formObj.nameValue) {
        this.error_name = true;
        return false;
      }
      if (!checkName(this.formObj.nameValue)) {
        this.$toast("请填写正确格式的名字");
        return false;
      }
      if (!this.formObj.reasonValue) {
        this.error_reason = true;
        return false;
      }
      if (!this.formObj.phoneValue) {
        this.error_phone = true;
        return false;
      }
      if (!checkPhoneNo(this.formObj.phoneValue)) {
        this.$toast("请填写正确格式的电话号码");
        return false;
      }
      if (!this.formObj.addressValue) {
        this.error_address = true;
        return false;
      }
      if (!this.formObj.startTimeValue) {
        this.error_startTime = true;
        return false;
      }
      if (!this.formObj.endTimeValue) {
        this.error_endTime = true;
        return false;
      }
      if (this.formObj.carNumValue && !checkPlateNo(this.formObj.carNumValue)) {
        this.$toast("请填写正确格式的车牌号");
        return false;
      }
      return true;
    },
    submitForm() {
      // this.checkForm();
      if (!this.checkForm()) {
        return;
      }
      if (this.picUpload) {
        // ftp上传成功则添加人脸信息
        this.addFaceInfo();
        return;
      } else {
        this.getSubmit(); // 提交表单
      }
    },
    getSubmit() {
      this.loading = true;

      let data = {
        stationId: 1,
        source: 2,
        booker: "物业",
        // userName: this.custNews.USER_NAME,
        // userCode: this.custNews.USER_CODE,
        guest: this.formObj.nameValue,

        // 访客角色信息
        guestType: this.formObj.reasonId,
        guestTypeName: this.formObj.reasonValue,
        guestTypeTime: this.formObj.reasonTime,

        guestPhone: this.formObj.phoneValue,
        doorNum: this.formObj.addressValue,
        roomCode: this.roomCode,
        roomName: this.roomName,

        startTime: this.formObj.startTimeValue,
        endTime: this.formObj.endTimeValue,

        isCar: this.formObj.carNumValue ? "1" : "0",
        plateNum: this.formObj.carNumValue,
        bookType: this.formObj.greenChannel,
        face: this.facePic ? "1" : "0",
        faceId: this.facePic ? this.faceId : "",
        facePicId: this.facePic ? this.fileupload.id : ""
      };
      // console.log("--提交预约入参--");
      // console.log(JSON.stringify(data));
      let detailData = {
        guest: this.formObj.nameValue,
        guestPhone: this.formObj.phoneValue,
        doorNum: this.formObj.addressValue,
        startTimeStr: this.startTimeValue,
        endTimeStr: this.endTimeValue,
        endTime: dayjs(this.formObj.endTimeValue).toISOString(),
        face: this.facePic ? "1" : "0",
        plateNum: this.formObj.carNumValue
      };
      bookingInfo(data)
        .then(res => {
          this.loading = false;
          if (res.code == "200") {
            // if (this.facePic) {
            //   // 如果有人脸预约成功，就下发设备
            //   console.log("调用下发设备接口");
            //   this.face2Device();
            // }
            Object.assign(detailData, { id: res.data.id });
            this.$router.push({
              path: "/success",
              query: detailData
            });
          } else {
            this.$toast(res.message);
          }
        })
        .catch(err => {
          this.loading = false;
          this.$toast(err);
        });
    },
    // 获取剩余车位
    getParkingLeftNum() {
      this.loading = true;
      this.noParkRemain = false; // 车牌号码input和radio

      let data = {
        stationid: 1
      };
      getParkingInfo(data)
        .then(res => {
          console.log("获取车位数据", res);
          this.loading = false;
          if (res.code == 200) {
            this.leftNum = res.data.remain;
          } else {
            this.$toast(res.message);
          }
          if (this.leftNum <= 0) {
            this.leftNumDialog = true;
            this.noParkInput = true; // 车牌号码和radio禁用
            this.noParkRadio = true; // 车牌号码和radio禁用
          }
        })
        .catch(err => {
          this.loading = false;
          this.leftNum = 0;
          this.leftNumDialog = true;
          this.noParkInput = true; // 车牌号码和radio禁用
          this.noParkRadio = true; // 车牌号码和radio禁用
          console.log("获取剩余车位失败");
          this.$toast(err);
        });
    },
    // 获取绿色通道时间
    getGreenTime() {
      this.loading = true;

      let data = {
        stationid: 1
      };
      getGreenTime(data)
        .then(res => {
          console.log("获取绿色通道失败", res);
          this.loading = false;
          if (res.code == 200) {
            let time = parseInt(res.data.value);
            this.greenTimeInterval = time; //绿色时间间隔分钟形式
            let hour = parseInt(time / 60);
            let minute = time % 60;
            this.greenTimeIntervalStr =
              hour == 0 ? `${minute}分钟` : `${hour}小时${minute}分钟`;
          } else {
            this.$toast(res.message);
          }
        })
        .catch(err => {
          this.loading = false;
          this.$toast(err);
          console.log("获取绿色通道失败", err);
        });
    },
    // 点击照相机
    onClickedGetPhoto(item, index) {
      this.getPictureData(index);
    },
    // 通过摄像头或者本地相册进行图片上传
    getPictureData(selectIndex) {
      selectIndex ? this.getLocalPicture() : this.useCamera();
    },
    // 通过摄像头获取
    useCamera() {
      this.$plugin.usePlugin("camera")(
        imageData => {
          this.picUrl = imageData;
          // if (/(Android)/i.test(navigator.userAgent)){
          //   this.getLocalPicture();
          // } else {
          this.uploadPhotoFile(this.picUrl);
          // }
        },
        error => {
          this.$toast(error);
        }
      );
    },
    // 通过相册获取
    getLocalPicture() {
      this.$plugin.usePlugin("getPictures")(
        1,
        images => {
          images.forEach(imageData => {
            this.picUrl = imageData;
            this.uploadPhotoFile(this.picUrl);
          });
        },
        error => {
          this.$toast(error);
        }
      );
    },
    // 上传照片
    uploadPhotoFile(fileURL) {
      console.log("--uploadPhotoFile--");
      let loading = store.get("showLoadingView");
      if (loading) {
        loading(true);
      }
      let reqUrl = storage.getStorage("xtBaseUrl") + "/web/face/check/pic";
      let options = new FileUploadOptions();
      let params = {};
      params.file = fileURL;
      params.type = "face";
      options.params = params;
      let token = storage.getStorage("controlToken");
      options.headers = { contentType: "multipart/form-data" };
      options.headers = { Authorization: token };
      let success = res => {
        if (loading) {
          loading(false);
        }
        let resData = JSON.parse(res.response);
        console.log(resData.code);
        console.log(JSON.stringify(resData.data.fileupload));
        if (resData.code == 200) {
          console.log("--uploadPhotoFile--true--");
          this.fileupload = resData.data.fileupload; // 照片的信息
          this.faceId = resData.data.Id; // 人脸的id
          // this.addFaceInfo();
          this.picUpload = true;
          this.$toast("上传成功");
        } else {
          console.log("--uploadPhotoFile--false--");
          this.picUpload = false;
          this.picUrl = "";
          this.$toast(resData.message);
        }
      };
      let fail = err => {
        if (loading) {
          loading(false);
        }
        console.log(JSON.stringify(err));
        this.picUpload = false;
        this.picUrl = "";
        this.$toast("上传失败，请重新上传");
      };
      let ft = new FileTransfer();
      console.log("fileUrl", fileURL);
      console.log("reqUrl", reqUrl);

      ft.upload(fileURL, encodeURI(reqUrl), success, fail, options);
      // this.$plugin.usePlugin("fileTransfer")(
      //   reqUrl,
      //   encodeURI(fileURL),
      //   success,
      //   fail,
      //   options
      // );
    },
    // 上传人脸信息
    addFaceInfo() {
      console.log("--addFaceInfo--");
      this.loading = true;

      let data = {
        code: this.faceId,
        phone: this.formObj.phoneValue,
        faceSource: 2,
        personType: 2,
        isBlack: 1,
        groupId: 2,
        groupName: "访客",
        isFlag: 1,
        isActive: 1,

        id: this.faceId,
        name: this.formObj.nameValue,
        faceUrl: this.fileupload.location,
        faceFileName: this.fileupload.fileNewName,
        faceFileId: this.fileupload.id,
        validStart: this.startTimeValue + ":00",
        validEnd: this.endTimeValue + ":00",

        roomCode: this.roomCode,
        roomName: this.roomName
      };
      console.log(JSON.stringify(data));
      addFaceInfo(data)
        .then(res => {
          this.loading = false;
          console.log(JSON.stringify(res));
          if (res.code == "200") {
            // 此时已成功上传人脸信息
            this.facePic = true;
            console.log("调用下发设备接口");
            this.face2Device();
            // this.getSubmit(); // 提交表单
          } else {
            this.facePic = false;
            this.getSubmit(); // 提交表单
            this.$toast("上传人脸信息失败，请使用二维码开门");
          }
        })
        .catch(err => {
          this.loading = false;
          this.facePic = false;
          this.$toast(err);
        });
    },
    // 下发设备
    face2Device() {
      console.log("--face2Device--");
      this.loading = true;

      face2Device(this.faceId, this.custNews.AREA_CODE)
        .then(res => {
          this.loading = false;
          console.log(this.faceId);
          console.log(JSON.stringify(res));
          if (res.code == "200") {
            // 此时已成功上传人脸信息
            this.$toast("成功下发设备");
            this.getSubmit();
          } else {
            this.$toast("下发设备失败:" + res.code);
          }
        })
        .catch(err => {
          this.loading = false;
          this.$toast("下发设备失败:" + err.code);
        });
    }
  }
};
</script>
